@import url(https://fonts.googleapis.com/css?family=Galindo|Lato|Open+Sans|Skranji&display=swap);
.bgParticles {
    position: fixed;
    z-index: 0;
}

#PageContent {
    align-items: center;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    position: absolute;
    z-index: 1;
    width: 100%;
}

.crash {
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: center;
    width: 100vw;
}
#landing {
    align-items: center;
    display: flex;
    font-family: 'Skanji', cursive;
    height: 100vh;
    justify-content: center;
    text-align: center;
    width: 75%;
}

#landing .selfie {
    border-radius: 25%;
    height: 350px;
    width: 250px;
}

#landing .sign {
    align-items: center;
    -webkit-animation-name: leftSectionDesc;
            animation-name: leftSectionDesc;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-delay: 1.5s;
            animation-delay: 1.5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    display: flex;
    justify-content: center;
    opacity: 0;
}

#landing .left {
    -webkit-animation-name: leftSection;
            animation-name: leftSection;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-delay: 500ms;
            animation-delay: 500ms;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    opacity: 0;
    width: 100%;
}

#landing .right {
    align-items: center;
    -webkit-animation-name: rightSection;
            animation-name: rightSection;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    display: flex;
    flex-direction: column;
    justify-content: center;
    opacity: 0;
    width: 100%;
}

#landing .title {
    cursor: pointer;
    font-family: 'Galindo', cursive;
    font-weight: bold;
    font-size: 2.5rem;
    -webkit-margin-after: 4px;
            margin-block-end: 4px;
    height: 72px;
    width: 295px;
}

#landing .title .animatedLetter {
    transition-duration: 250ms;
}

#landing .title .animatedLetter:hover {
    color: #f44336;
    font-size: 3rem;
    padding-left: 4px;
    padding-right: 4px;
}

#landing .proffesions {
    font-size: 1.5rem;
    font-weight: bold;
}

#landing .desc {
    -webkit-animation-name: rightSectionDesc;
            animation-name: rightSectionDesc;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    font-family: 'Lato', sans-serif;
    margin-top: 32px;
    opacity: 0;
    text-align: justify;
    width: 75%;
}

#landing .desc a {
    color: rgba(255, 255, 255, 1);
    border-bottom: dashed 1px white;
    text-decoration: none;
    transition-duration: 250ms;
}

#landing .desc a:hover {
    border-bottom: dotted 1px white;
    color: hsla(0, 0%, 100%, .75);
}

@media (max-width: 768px) {
    #landing {
        flex-direction: column;
        height: 100%;
        padding-top: 64px;
        width: 90%
    }
    #landing .left {
        padding-top: 32px;
    }
    #landing .right {
        margin-left: unset;
    }
    #landing .right .desc {
        width: 90%;
    }
    #landing .selfie {
        height: 250px;
        width: 175px;
    }
}

@-webkit-keyframes leftSection {
    from {
        opacity: 0;
        transform: translateX(-250px);
    }
    to {
        opacity: 1;
        transform: translateX(0px);
    }
}

@keyframes leftSection {
    from {
        opacity: 0;
        transform: translateX(-250px);
    }
    to {
        opacity: 1;
        transform: translateX(0px);
    }
}

@-webkit-keyframes leftSectionDesc {
    from {
        opacity: 0;
        transform: translateY(-50px);
    }
    to {
        opacity: 1;
        transform: translateY(0px);
    }
}

@keyframes leftSectionDesc {
    from {
        opacity: 0;
        transform: translateY(-50px);
    }
    to {
        opacity: 1;
        transform: translateY(0px);
    }
}

@-webkit-keyframes rightSection {
    from {
        opacity: 0;
        transform: translateX(250px);
    }
    to {
        opacity: 1;
        transform: translateX(0px);
    }
}

@keyframes rightSection {
    from {
        opacity: 0;
        transform: translateX(250px);
    }
    to {
        opacity: 1;
        transform: translateX(0px);
    }
}

@-webkit-keyframes rightSectionDesc {
    from {
        opacity: 0;
        transform: translateY(50px);
    }
    to {
        opacity: 1;
        transform: translateY(0px);
    }
}

@keyframes rightSectionDesc {
    from {
        opacity: 0;
        transform: translateY(50px);
    }
    to {
        opacity: 1;
        transform: translateY(0px);
    }
}
