nav {
    background-color: rgba(33, 33, 33, 0);
    font-family: "Open Sans", sans-serif;
    font-size: 14pt;
    height: 64px;
    position: fixed;
    top: 0;
    width: 100%;
    transition-duration: 250ms;
    z-index: 2;
}

nav.focus {
    background-color: rgba(33, 33, 33, 1);
}

nav ul {
    list-style-type: none;
}

nav ul li {
    float: left;
}

nav ul li a {
    color: hsla(0, 0%, 100%, .5);
    margin-right: 16px;
    transition-duration: 250ms;
    text-decoration: none;
}

nav ul li a:hover {
    color: hsla(0, 0%, 100%, .75);
}

nav ul li a svg {
    margin-right: 8px;
}

nav .branding {
    font-family: "Skanji", cursive;
    font-size: 1.25rem;
    float: right;
    margin-right: 16px;
}

nav .branding a {
    color: white;
    text-decoration: none;
}

nav .hamburger {
    display: none;
}

@media (max-width: 800px) {
    nav .sections {
        background-color: rgba(33, 33, 33, 1);
        display: flex;
        flex-direction: column;
        position: absolute;
        left: 0;
        top: -256px;
        transition-duration: 500ms;
        z-index: -1;
    }
    nav .sections.active {
        top: 44px;
    }
    nav .sections a {
        align-items: center;
        display: flex;
        padding-top: 12px;
        padding-bottom: 12px;
        padding-left: 32px;
        width: 100vw;
    }
    nav .sections a:hover {
        background-color: rgb(17, 17, 17);
    }
    nav .branding {
        display: none;
    }
    nav .hamburger {
        display: block;
        position: fixed;
        left: 32px;
        top: 16px;
        transition-duration: 500ms;
    }
    nav .hamburger.active {
        transform: rotate(90deg);
    }
}
.BackToTop {
    bottom: 16px;
    color: white;
    position: fixed;
    opacity: 0;
    right: 16px;
    transition-duration: 500ms;
    z-index: 2;
}

.BackToTop:hover {
    cursor: pointer;
    color: #e53935;
}

.BackToTop.visible {
    opacity: 1;
}
.preloader {
    align-items: center;
    background: #263238;
    display: flex;
    flex-direction: column;
    font-family: 'Open Sans', sans-serif;
    height: 100vh;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    transition-duration: 500ms;
    width: 100vw;
    z-index: 3;
}

.preloader .text {
    margin-top: 32px;
}

.preloader.loaded {
    opacity: 0;
}

.preloader.hidden {
    visibility: hidden;
}
